<template>
  <v-card height="auto">
    <v-card-title>
      <h4>{{ trackInfo.artist }} - {{ trackInfo.title }}</h4>
      <v-spacer></v-spacer>
      <h4>{{ trackInfo.seek | minutes }}/{{ trackInfo.duration | minutes }}</h4>
    </v-card-title>    
  </v-card>
</template>

<script>
  export default {
    props: {
      trackInfo: Object
    },
  }
</script>


